import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

function CreateEvent() {
  const [eventInfo, setEventInfo] = useState({
    name: '',
    description: '',
    date: '',
    location: '',
    link: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEventInfo((prevInfo) => ({
      ...prevInfo,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle form submission or API call here with the eventInfo
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(eventInfo),
    };
  
    fetch('/api/create-event/', requestOptions)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => {
        // Redirect to the /event/ page with the event code
        window.location.href = `/event/${data.code}`;
      })
      .catch((error) => console.error('Error:', error.message));
  };

  return (
    <div>
      <h1>Create Event</h1>
      <form onSubmit={handleSubmit}>
        <TextField
          label="Event Name"
          name="name"
          value={eventInfo.name}
          onChange={handleChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Event Description"
          name="description"
          value={eventInfo.description}
          onChange={handleChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Event Date"
          name="date"
          type="datetime-local" // Use datetime-local input type for date and time
          value={eventInfo.date}
          onChange={handleChange}
          fullWidth
          margin="normal"
          InputLabelProps={{
            shrink: true,
          }}
        />
        <TextField
          label="Event Location"
          name="location"
          value={eventInfo.location}
          onChange={handleChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Event Link"
          name="link"
          value={eventInfo.link}
          onChange={handleChange}
          fullWidth
          margin="normal"
        />
        <Button type="submit" variant="contained" color="primary">
          Create Event
        </Button>
      </form>
    </div>
  );
}

export default CreateEvent;
